.loader{
    width: 65px;
    height: 65px;
    border-radius: 50%;
    border: 5px dotted #66FCF1;
    animation: rotate 2s infinite linear;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(3);
    }
    
}